/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

img {
  max-width: 100%;
  height: auto; }

:root {
  --white: #ffffff;
  --gray: #dcdcdc;
  --gray-100: #f2f2f2;
  --gray-200: #00000020;
  --gray-300: #ced4da;
  --gray-350: #6c6c6c;
  --gray-400: #495057;
  --gray-500: #555555;
  --gray-550: #707070;
  --gray-600: #212529;
  --gray-700: #0a0a0a;
  --gray-900: #111619;
  --black: #000000;
  --red-500: #aa0601;
  --pink: #fbf2f2;
  --pink-100: #aa06010d;
  --brown: #5f3e2d;
  --light-brown: #b75929;
  --red-700: #9f1213;
  --red-800: #650300; }

.container {
  margin: 0 auto;
  max-width: 1100px; }
  @media screen and (max-width: 767px) {
    .container {
      padding: 0 15px; } }

.main {
  margin: 0 auto;
  padding: 80px 0 160px 0;
  max-width: 1110px;
  min-height: 40vh; }
  @media screen and (max-width: 1199px) {
    .main {
      max-width: 100%;
      padding: 56px 39px; } }
  @media screen and (max-width: 767px) {
    .main {
      padding: 32px 15px 24px 15px; } }
  .main.location {
    padding: 96px 0 160px 0; }
    @media screen and (max-width: 1199px) {
      .main.location {
        max-width: 100%;
        padding: 60px 30px 96px 39px; } }
    @media screen and (max-width: 767px) {
      .main.location {
        padding: 28px 15px 56px 15px; } }
  .main.blog {
    padding: 40px 0 160px 0; }
    @media screen and (max-width: 1199px) {
      .main.blog {
        max-width: 100%;
        padding: 60px 30px 96px 39px; } }
    @media screen and (max-width: 767px) {
      .main.blog {
        padding: 28px 15px 56px 15px; } }

